<template>
  <div style="display: flex; align-items: center;">
    <div class="item">
      <div class="up">
        <h4
          class="txt1"
          style="white-space: pre-line; height: 48px !important;"
        >
          {{
            value.titulo.length > 56
              ? value.titulo.substring(0, 55) + '...'
              : value.titulo
          }}
        </h4>
        <v-icon
          v-animate-onscroll.repeat="{ down: 'animated bounceIn' }"
          x-large
          color="verde"
        >
          {{ value.icono }}
        </v-icon>
        <p>
          {{
            value.texto.length > 250
              ? value.texto.substring(0, 250) + '...'
              : value.texto
          }}
        </p>
      </div>
      <div style="margin-top: 20px;">
        <!-- <h4 class="txt2">
                Compromiso de servicio al 1000000%
              </h4> -->
        <!-- Que lleve a los mini cvs de los profesionales. -->
        <v-btn
          class="success"
          :to="value.to"
          :href="value.href"
          :target="(value.href) ? '_blank' : '_self'"
        >
          <span>{{ value.textoBoton }}</span>
        </v-btn>
      </div>
    </div>
    <div
      v-if="value.index !== value.total - 1"
      class="divider"
    />
  </div>
</template>

<script>
  export default {
    props: {
      value: {
        titulo: {
          type: Text,
          default: '',
        },
        icono: {
          type: Text,
          default: '',
        },
        texto: {
          type: Text,
          default: '',
        },
        textoBoton: {
          type: Text,
          default: '',
        },
        index: {
          type: Number,
          default: '',
        },
        total: {
          type: Number,
          default: '',
        },
        to: {
          type: Text,
          default: '',
        },
        href: {
          type: Text,
          default: '',
        },
        type: Object,
        default: null,
      },
    },
  }
</script>
<style scoped>
.item {
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.item .txt2 {
  margin-bottom: 20px;
  color: green;
}

.item > .txt1,
.main > .item > p {
  color: #595959;
}

.item .up > p {
  margin-top: 20px;
  text-align: justify;
  text-justify: auto !important;
}

.item .v-icon {
  margin-top: 20px;
}

.item .v-btn {
  border-radius: 20px;
  text-transform: none !important;
}

.divider {
  width: 1.5px;
  height: 250px;
  margin: 6px 0;
  background: #36ba7f;
}
</style>
